import { Component, OnInit, Input, Output, EventEmitter, TemplateRef, Optional, Injector, ViewChild, AfterViewInit, SimpleChanges, OnChanges } from '@angular/core';
import { CloudprintService, OutputType, FileType, RetrieveParam } from '@gsp-svc/cloudprint';
import { CacheService } from '@ecp-caf/caf-common';
import { MessagerService } from '@farris/ui-messager';
import { IntegrationPrintService, QDPCacheUtil, RtfServices } from '@qdp/common';
import { LocalizeService, QDP_EN_US } from '@qdp/localize';
import { QueryFrameworkPaginationComponent } from '../qf-pagination/qf-pagination.component';
import { ConditionDialogComponent } from '../condition-dialog/condition-dialog.component';
import { ParameterUtils } from '@qdp/spread';

@Component({
  selector: 'query-framework-toolbar',
  templateUrl: './qf-toolbar.component.html',
  styleUrls: ['./qf-toolbar.component.css']
})
export class QueryFrameworkToolbarComponent implements OnInit, AfterViewInit, OnChanges {
  @Input() queryId: any;
  @Input() groupType: any;
  @Input() queryName: any;
  @Input() controlType: any;
  @Input() isMultiForm: boolean; // æ¯å¦åµå¥å¼è¡¨å
  editorParams: any;
  @Input() schemaId: any;
  @Input() schemaInfo: any;
  @Input() colList: any;
  @Input() pageInfo: any;
  @Input() isShowToolbar: any = true;
  @Input() queryRelativeUrl: any;
  @Input() qoManagerCode: any;
  @Input() conditionExpression: any;
  @Output() schemaSelected = new EventEmitter<any>();
  @Output() dialogBeforeClose = new EventEmitter<any>();
  @Output() pageChange = new EventEmitter<any>();
  @Output() exportExcel = new EventEmitter<any>();
  @Output() printIntegration = new EventEmitter<any>();
  @Output() printPreview = new EventEmitter<any>();
  @Output() refreshEvent = new EventEmitter<any>();
  @Output() filterCondition = new EventEmitter<any>();
  @Output() clearCache = new EventEmitter<any>();
  @Output() expendCollapseLevel = new EventEmitter<any>();
  @Output() clearSort = new EventEmitter<any>();
  @Input() inputTemplate: TemplateRef<any>;
  @Input() qoId: any;
  @Input() voId: any;
  @Input() qdpView: any;
  @Input() templateType: any;
  @Input() showSchemaManager: any = true;
  @Input() showExport: any = true;
  @Input() showReFresh: any = true;
  @Input() showFilter: any = true;
  @Input() showPrint: any = true;
  @Input() showClearSort: any = true;
  @Input() showClearCache: any = true;
  @Input() showPreSchema: any = true;
  // @Input() showFormPrint: any = true;
  // @Input() showPagination: any = true;
  @Input() formPrintOption = {
    isShowFormPrint: false,
    printBEId: '',
    printFormIdField: '',
    dim1: '',
    dim2: ''
  };
  isShowMore = false; // æ¯å¦æ¾ç¤ºæ´å¤
  isClickShowMore = false; // ç¹å»åæ¯å¦æ¾ç¤ºæ´å¤
  localizeService: any;
  localizePageInfo: any;
  cprintService: any;
  @ViewChild(QueryFrameworkPaginationComponent) paginationComponent: QueryFrameworkPaginationComponent;
  @Input() conditionDialog: ConditionDialogComponent;
  getConditionDialog() {
    if (this.conditionDialog) {
      return this.conditionDialog;
    } else if (QDPCacheUtil.getCache(this.tabId + 'advanceTemplate-condition-dialog')) {
      return QDPCacheUtil.getCache(this.tabId + 'advanceTemplate-condition-dialog');
    }
  }

  svgShuaxin = true;
  svgFilter = true;
  svgDimesion = true;
  svgClearCache = true;
  svgPrint = true;
  isExpand = false;
  printShow = false;
  integrationPrintService: IntegrationPrintService;
  msgService: MessagerService;
  cache: CacheService;
  tabId: any;

  constructor(private cloudprintService: CloudprintService, private cacheservice: CacheService, @Optional() private injector: Injector) {
    this.cprintService = this.cloudprintService;
    if (this.injector) {
      this.localizeService = this.injector.get(LocalizeService);
      this.integrationPrintService = new IntegrationPrintService(this.cloudprintService);
      this.msgService = this.injector.get(MessagerService);
      this.cache = this.injector.get(CacheService);
      this.localizePageInfo = this.localizeService.getValue('queryFramework.pageInfo');
      this.tabId = RtfServices.getTabId(this.queryId);
    }
  }

  ngOnInit() {
    if (this.controlType === 'list' || this.controlType === 'treelist') {
      this.printShow = true;
    } else {
      this.printShow = false;
    }
    if (this.inputTemplate) {
      this.isShowMore = true;
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    for (const changeName in changes) {
      const newValue = changes[changeName].currentValue;
      if (newValue !== null && newValue !== void 0) {
        switch (changeName) {
          case 'queryId':
            this.tabId = RtfServices.getTabId(newValue);
            break;
          default:
            break;
        }
      }
    }
  }

  ngAfterViewInit() {
  }

  handleBeforeClose(id: any) {
    this.dialogBeforeClose.emit(id);
  }

  refresh() {
    this.refreshEvent.emit();
  }

  filter() {
    this.filterCondition.emit();
  }

  handleClearSort() {
    this.clearSort.emit();
  }

  condition() {
    if (this.getConditionDialog()) {
      this.getConditionDialog().handleDialogShow();
      this.getConditionDialog().handleInfoChanged(0);
      this.getConditionDialog().Id = this.getConditionDialog().schemaId;
    }
  }

  handleDimesionSetting() {
    this.qdpView.toggle();
  }

  handlePrintIntegration($event: any) {
    this.printIntegration.emit($event);
  }

  handlePrintPreview($event: any) {
    this.printPreview.emit($event);
  }

  handleClearCache() {
    this.clearCache.emit();
  }

  print() {
    if (this.controlType !== 'crosstab' && this.controlType !== 'charts') {
      const params = this.createParameters(Object.assign({}, {
        queryId: this.queryId,
        controlType: this.controlType,
        schemaId: this.schemaId,
        filtercondition: this.conditionExpression,
        qoManagerCode: this.qoManagerCode,
        voId: this.voId
      }, { pageIndex: 0, pageSize: 0 }))

      const sheet = this.qdpView.spread.getActiveSheet();
      const selection = sheet.getSelections();
      let formIdField = '';
      let beId = '';
      let dim1 = '';
      let dim2 = '';
      if (this.formPrintOption && this.formPrintOption.printBEId) {
        beId = this.formPrintOption.printBEId;
        params['beId'] = beId;
      } else {
        this.msgService.warning(this.localizeService.getValue('queryFramework.message.validate.beMetadataIdEmpty'));
        return;
      }
      if (this.formPrintOption && this.formPrintOption.printFormIdField) {
        formIdField = this.formPrintOption.printFormIdField;
        params['formIdField'] = formIdField;
      } else {
        this.msgService.warning(this.localizeService.getValue('queryFramework.message.validate.formIdFieldEmpty'));
        return;
      }
      if (this.formPrintOption && this.formPrintOption.dim1) {
        dim1 = this.formPrintOption.dim1;
      }
      if (this.formPrintOption && this.formPrintOption.dim2) {
        dim2 = this.formPrintOption.dim2;
      }
      let ids = '';
      if (selection && selection.length) {
        for (let s = 0; s < selection.length; s++) {
          for (let i = selection[s].row; i < (selection[s].rowCount + selection[s].row); i++) {
            const dataItem = sheet.getDataItem(i);
            if (dataItem && dataItem.hasOwnProperty(formIdField)) {
              ids += ',' + dataItem[formIdField];
            }
          }
        }
        ids = ids.substring(1);
        this.integrationPrintService.printByIdsWithDimension(beId, ids, dim1, dim2);
      } else {
        ids = '1';
        this.integrationPrintService.printWithDimensionCustomDataSource(beId, ids, dim1, dim2, 'InternalService', 'com.inspur.edp.qdp.lcp.print.FormPrintDataManager', params);
      }
      sheet.clearSelection();
    }
  }

  handleHover(type: any, state: any) {
    switch (type) {
      case 0:
        this.svgShuaxin = state === 1;
        break;
      case 1:
        this.svgFilter = state === 1;
        break;
      case 2:
        this.svgDimesion = state === 1;
        break;
      case 3:
        this.svgClearCache = state === 1;
        break;
      case 4:
        this.svgPrint = state == 1;
        break;
      default:
        break;
    }
  }

  handleExpand($event) {
    this.isExpand = $event;
    this.expendCollapseLevel.emit(this.isExpand);
  }

  private createParameters(option: any) {
    const tabId = RtfServices.getTabId(option.queryId);
    let entityData: any;
    if (this.cache.get(tabId)) {
      entityData = JSON.stringify(this.cache.get(tabId));
    } else if (this.cache.get(option.queryId)) {
      entityData = JSON.stringify(this.cache.get(option.queryId));
    } else {
      entityData = JSON.stringify({ 'id': 'undefined_null' });
    }
    const params = {
      entityData: entityData,
      extendCond: this.cache.get(tabId + 'extendCond') ? this.cache.get(tabId + 'extendCond') : option.extendCond,
      pageIndex: option.pageIndex,
      pageSize: option.pageSize,
      schemaId: option.schemaId && option.schemaId !== 'prefab' ? option.schemaId : '1',
      filterCond: option.filterCondition ? option.filterCondition : (this.cache.get(tabId + 'filtercondition') ? this.cache.get(tabId + 'filtercondition') : ''),
      qoManagerCode: option.qoManagerCode ? option.qoManagerCode : '',
      voCode: option.voId ? option.voId : '',
    };
    try {
      const parameterEntityData = JSON.parse(entityData);
      if (parameterEntityData.hasOwnProperty('@filterExpressionString@')) {

        const filterExpression = params && params.filterCond ? JSON.parse(params.filterCond) : null;
        if (!filterExpression) {
          params.filterCond = parameterEntityData['@filterExpressionString@'];
        } else {
          const advanceFilterExpression = JSON.parse(parameterEntityData['@filterExpressionString@']);
          advanceFilterExpression.expressItems = advanceFilterExpression.expressItems.concat(filterExpression.expressItems);
          params.filterCond = JSON.stringify(advanceFilterExpression);
        }
      }
    } catch (e) {
    }
    if (option.controlType) {
      params['controlType'] = option.controlType;
    }
    if (option.excelExportName) {
      params['excelExportName'] = option.excelExportName;
    }
    if (option.printIntegration) {
      params['printIntegration'] = option.printIntegration ? JSON.stringify(option.printIntegration) : '';
    }

    return params;
  }
}
