import { Component, OnInit, ViewChild, Injector, Input, SimpleChanges, OnChanges } from '@angular/core';
import { DialogComponent } from '@farris/ui-dialog';
import { LocalizeService } from '@qdp/localize';
import { ConditionSchemaService } from '@qdp/condition-schema';
import { MessagerService } from '@farris/ui-messager';
import { NotifyService, NotifyOptions } from '@farris/ui-notify';
import { Subject } from 'rxjs';
import { FormErrorService } from '@farris/command-services';

@Component({
  selector: 'condition-manager-dialog',
  templateUrl: './condition-manager-dialog.component.html',
  styleUrls: ['./condition-manager-dialog.component.css']
})
export class ConditionManagerDialogComponent implements OnInit, OnChanges {
  @ViewChild('farrisDialog') farrisDialog: DialogComponent;
  @Input() currentSchema: any;
  localizeService: any;
  expandPublic = true;
  expandPersonal = true;
  @Input() queryId: any;
  @Input() publicList = [];
  @Input() personalList = [];
  removeList = [];
  motifyList = [];
  removeListEvent = new Subject<any>();
  formErrorService: any;

  constructor(
    private injector: Injector,
    private msgService: MessagerService,
    private conditionService: ConditionSchemaService,
    private notifyService: NotifyService
  ) {
    if (this.injector) {
      this.localizeService = this.injector.get(LocalizeService);
      this.formErrorService = this.injector.get(FormErrorService);
    }
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
  }

  handleDialogShow() {
    this.farrisDialog.show();
  }

  handleExpandType($event: any) {
    this.expandPublic = $event === 0 ? !this.expandPublic : this.expandPublic;
    this.expandPersonal = $event === 1 ? !this.expandPersonal : this.expandPersonal;
  }

  handleNgModelChange(change: any, item: any) {
    item.isDefault = change ? 1 : 0;
  }

  handleDeleteSchame($event: any) {
    if (this.removeList.findIndex((item: any) => item.id === $event.id) < 0) {
      this.removeList.push($event);
      this.motifyList.push({ type: 'delete', id: $event.id });
    }
    const index = $event.schemaType === 0
      ? this.publicList.findIndex((item: any) => item.id === $event.id)
      : this.personalList.findIndex((item: any) => item.id === $event.id);
    if ($event.schemaType === 0) {
      this.publicList.splice(index, 1);
    } else {
      this.personalList.splice(index, 1);
    }
  }

  handleSettingDefault($event: any) {
    const self = this;
    this.publicList.forEach((item: any) => {
      item.isDefault = item.id === $event.id ? 1 : 0;
      if (item.id === $event.id) {
        self.motifyList.push({ type: 'edit', id: $event.id });
      }
    });
    this.personalList.forEach((item: any) => {
      item.isDefault = item.id === $event.id ? 1 : 0;
      if (item.id === $event.id) {
        self.motifyList.push({ type: 'edit', id: $event.id });
      }
    });
  }

  handleOKClick() {
    const self = this;
    if (this.motifyList.length) {
      const deleteSchemas = [];
      let saveSchema: any;
      this.motifyList.forEach((item: any) => {
        if (item.type === 'delete') {
          deleteSchemas.push(item.id);
        } else {
          saveSchema = self.publicList.find((el: any) => el.id === item.id);
          if (!saveSchema || !saveSchema.id) {
            saveSchema = self.personalList.find((el: any) => el.id === item.id);
          }
        }
      });
      if (saveSchema && saveSchema.schemaValue) {
        saveSchema.schemaValue = saveSchema && saveSchema.schemaValue ? JSON.stringify(saveSchema.schemaValue) : null;
      }
      if (deleteSchemas.length && saveSchema && saveSchema.id) {
        this.conditionService.deleteSchemaList(deleteSchemas.join(','), self.queryId).subscribe(() => {
          this.conditionService.saveSchema(saveSchema).subscribe(() => {
            self.clearChanges();
            self.notifyService['success']({
              title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.success'), timeout: 3000
            } as NotifyOptions);
            self.farrisDialog.close();
          }, (error: any) => {
            self.formErrorService.exception(error.Message, error);
            // self.notifyService['success']({
            //   title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.error'), timeout: 3000
            // } as NotifyOptions);
            self.farrisDialog.close();
          });
        }, (error: any) => {
          self.formErrorService.exception(error.Message, error);
          // console.log(error);
          // self.notifyService['success']({
          //   title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.error'), timeout: 3000
          // } as NotifyOptions);
          self.farrisDialog.close();
        });
      } else if (deleteSchemas.length) {
        this.conditionService.deleteSchemaList(deleteSchemas.join(','), self.queryId).subscribe(() => {
          self.clearChanges();
          self.notifyService['success']({
            title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.success'), timeout: 3000
          } as NotifyOptions);
          self.farrisDialog.close();
        }, (error: any) => {
          self.formErrorService.exception(error.Message, error);
          // console.log(error);
          // self.notifyService['success']({
          //   title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.error'), timeout: 3000
          // } as NotifyOptions);
          self.farrisDialog.close();
        });
      } else if (saveSchema && saveSchema.id) {
        this.conditionService.saveSchema(saveSchema).subscribe(() => {
          self.clearChanges();
          self.notifyService['success']({
            title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.success'), timeout: 3000
          } as NotifyOptions);
          self.farrisDialog.close();
        }, (error: any) => {
          self.formErrorService.exception(error.Message, error);
          // console.log(error);
          // self.notifyService['success']({
          //   title: self.localizeService.getValue('queryFramework.message.info.title'), msg: self.localizeService.getValue('queryFramework.message.operate.error'), timeout: 3000
          // } as NotifyOptions);
          self.farrisDialog.close();
        });
      }
    }
  }

  clearChanges() {
    this.motifyList = [];
    this.removeList = [];
  }

  handleCancelClick() {
    const self = this;
    if (this.motifyList.length) {
      this.motifyList.forEach((item: any) => {
        if (item.type === 'delete') {
          const result = self.removeList.find((el: any) => el.id === item.id);
          if (result.schemaType === 0) {
            self.publicList.push(item);
          } else {
            self.personalList.push(item);
          }
        } else {
          this.publicList.forEach((el: any) => {
            el.isDefault = el.id === item.id ? 1 : 0;
          });
          this.personalList.forEach((el1: any) => {
            el1.isDefault = el1.id === item.id ? 1 : 0;
          });
        }
      });
      this.removeList = [];
      this.motifyList = [];
    }
    this.farrisDialog.close();
  }
}
