import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'query-framework-pagination',
  templateUrl: './qf-pagination.component.html',
  styleUrls: ['./qf-pagination.component.css']
})
export class QueryFrameworkPaginationComponent implements OnInit, OnChanges {
  pagination: any = {
    pageIndex: 1,
    pageSize: 10,
    totalPage: 0
  };
  isDisablePreBtn = false;
  isDisableNextBtn = false;
  isEnable = true;
  // @Input() schemaInfo: any;
  // @Input() colList: any;
  @Input() pageInfo: any;
  @Input() localizePageInfo: any;
  totalCount = 21;
  pageSize = 10;
  pageIndex = 1;
  @Output() pageChange = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {
    this.initPageInfo();
  }

  ngOnChanges(changes: SimpleChanges) {
    for (const changeName in changes) {
      const newValue = changes[changeName].currentValue;
      if (newValue !== null && newValue !== void 0) {
        switch (changeName) {
          case 'schemaInfo':
            break;
          case 'pageInfo':
            if (newValue.pageSize === 0) {
              this.isEnable = false;
              return;
            } else {
              this.isEnable = true;
            }
            this.pagination.pageIndex = this.pageIndex = newValue.pageIndex;
            this.pagination.pageSize = this.pageSize = newValue.pageSize;
            this.pagination.totalPage = newValue.pageCount;
            this.totalCount = newValue.totalCount;
            this.initPageInfo();
            break;
        }
      }
    }
  }

  initPageInfo() {
    let totalPageCount = 0;
    if (this.totalCount <= this.pageSize) {
      totalPageCount = 1;
    } else {
      totalPageCount = (this.totalCount % this.pageSize) > 0
        ? Math.ceil(this.totalCount / this.pageSize)
        : (this.totalCount / this.pageSize);
    }
    this.pagination = {
      pageIndex: this.pageIndex,
      totalPage: totalPageCount
    };
    this.isDisablePreBtn = this.pagination.pageIndex === 1 ? true : false;
    this.isDisableNextBtn = this.pagination.pageIndex === this.pagination.totalPage ? true : false;

    if (!this.isEnable) {
      this.isDisablePreBtn = this.isDisableNextBtn = !this.isEnable;
    }
  }

  handlePreBtnClick(type: any) {
    switch (type) {
      case '0':
        this.pagination.pageIndex = 1;
        break;
      case '1':
        this.pagination.pageIndex -= 1;
        break;
    }
    this.isDisablePreBtn = this.pagination.pageIndex === 1 ? true : false;
    this.isDisableNextBtn = this.pagination.pageIndex === this.pagination.totalPage ? true : false;
    this.pageChange.emit(this.pagination);

  }

  handleNextBtnClick(type: any) {
    switch (type) {
      case '0':
        this.pagination.pageIndex = this.pagination.totalPage;
        break;
      case '1':
        this.pagination.pageIndex += 1;
        break;
    }
    this.isDisablePreBtn = this.pagination.pageIndex === 1 ? true : false;
    this.isDisableNextBtn = this.pagination.pageIndex === this.pagination.totalPage ? true : false;
    this.pageChange.emit(this.pagination);
  }

}
